export default {
  data: function () {
    return {
      key: this.$route.params.id,
      task:{}
    }
  },
  watch:{
    '$route'(to, from)
    {
      this.taskId = to.params.id;
    }
  },
  methods: {
    fetchTaskDetail: function () {

      this.$http.get('https://vuejs-http-7f2d8.firebaseio.com/data/'+ this.key +'.json')
        .then(response => {
          return response.json();
        })
        .then(data => {
          this.task = data;
        });
    }
  },
  beforeMount(){
    this.fetchTaskDetail()

  },
  template:'#todoDetail'
}



