export default{
  data: function () {
    return {
      newTask: "",
      tasks: [],
      count : 0,
      editingTask: {}
    }
  }, computed: {
    areAllSelected: function () {
      return this.tasks.every(function (task) {
        return task.checked;
      }) && this.tasks.length > 0;
    },
  },
  methods: {
    fetchData: function() {
      this.$http.get('https://vuejs-http-7f2d8.firebaseio.com/data.json')
        .then(response => {
          return response.json();
        })
        .then(data => {
          const resultArray = [];
          for (let key in data) {
            let obj = data[key];
            obj.key = key;
            resultArray.push(obj);
          }
          this.tasks = resultArray;
          this.count = resultArray.length;
        });
    },

    addTask: function () {
      var task = this.newTask.trim();
      if (task) {
        this.count++;
        var newTask = {text: task, checked: false, id: this.count};
        this.newTask = "";
      }
      this.$http.post('https://vuejs-http-7f2d8.firebaseio.com/data.json', newTask)
        .then(function (response) {
          this.fetchData();
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    removeTask: function (task) {
      /* var index = this.tasks.indexOf(task);
       this.tasks.splice(index, 1);
*/
      var deleteKey;
      for(let t in this.tasks)
      {
        var item = this.tasks[t];
        if(item.id === task.id)
          deleteKey = item.key;
      }

      this.$http.delete('https://vuejs-http-7f2d8.firebaseio.com/data/'+ deleteKey +'.json')
        .then(function () {
          this.fetchData();
        })
        .catch(function (error) {
          console.log(error);
        });


    },

    editTask: function (task) {
      this.editingTask = task;
    },

    endEditing: function (task) {
      this.editingTask = {};
      if (task.text.trim() === "") {
        this.removeTask(task);
      }
      else {
        let newTask = {text: task.text, checked: false, id: task.id};

        let updateKey;
        for(let t in this.tasks)
        {
          let item = this.tasks[t];
          if(item.id === task.id)
            updateKey = item.key;
        }

        this.$http.put('https://vuejs-http-7f2d8.firebaseio.com/data/'+updateKey+'.json', newTask)
          .then(function (response) {
            console.log(response);
          })
          .catch(function (error) {
            console.log(error);
          });
      }

    },

    clearList: function () {
      this.tasks = [];
    },

    selectAll: function (task) {
      var targetValue = this.areAllSelected ? false : true;
      for (var i = 0; i < this.tasks.length; i++) {
        this.tasks[i].checked = targetValue;
      }
    },

    check: function (task) {
      task.checked = true;
    },

    isChecked: function (task) {
      return task.checked;
    }
  },
  beforeMount(){
    this.fetchData()

  },
  template: '#my-todo'
}



